.offers{
  width: 65%;
  height: 60vh;
  display: flex;
  margin: auto;
  padding: 0px 140px;
  margin-bottom: 150px;
  margin-top: 150px;
  background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
}
.offers-left{
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  margin-top: 100px; 
}
.offers-left h1{
  columns: #171717;
  font-size: 80px;
  font-weight: 600;
}
.offers-left p{
  color: #171717;
  font-size: 22px;
  font-weight: 600;
}
.offers-left button{
  width: 282px;
  height: 70px;
  border: none;
  border-radius: 60px;
  padding: 0% 20px;
  background-color: #ff4141;
  color: white;
  font-size: 22px;
  font-weight: 500;
  margin-top: 30px;
  cursor: pointer;
}
.offers-left button:hover{
  background-color: #ff1a1a;
}
.offers-right{
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-top: 50px;
}